  font-size: 40px;
}

.Kanan {
  margin-left: 0;
}

@media only screen and (min-width: 850px) {
  .Navb-logo {
    margin-left: 30px;
  }

  .Navb-item {
    margin-left: 30px;
  }

  .Content {
    text-align: left;
  }

  .Nama {
    text-align: left;
    font-size: 85px;
    font-weight: 700;
  }

  .Kanan {
    margin-left: 5em;
  }

  .Keterangan {
    text-align: left;
    font-weight: 500;
    font-size: 35px;
  }

  .Teks-1 {
    text-align: left;
    font-weight: 500;
    font-size: 35px;
  }
}

.Kiri {
  display: none;
}

.HalamanContact {
  background-color: white;
}
.ButtonSubmit {
  border-radius: 20px;
  background-color: #f47522;
  padding: 5px 15px;
  color: white;
  margin: 0;
  border: 1px;
}

button:hover {
  background-color: #e4640f;
}

@media only screen and (min-width: 750px) {
  .Kiri {
    display: block;
  }

  .Logo {
    background-color: #19345ea6;
    height: 750px;
    width: 500px;
  }

  .Logo-alta {
    margin-bottom: 300px;
    opacity: 0 5;
  }
}

